<template>
  <div class="basic">
    <div class="form-container">
      <el-form label-position="right" label-width="120px" :rules="ruleList" ref="formData" :model="formData">
        <el-form-item label="" prop="nameCn">
          <el-input v-model="formData.nameCn" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="nameEn">
          <el-input v-model="formData.nameEn" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="homeUrl">
          <el-input v-model="formData.homeUrl" placeholder=""></el-input>
          <a :href="formData.homeUrl" target="_blank" v-if="formData.homeUrl" style="color:red"></a>
        </el-form-item>
        <el-form-item label="logo" prop="logoUrl" required>
          <el-upload class="upload-box" name="logo" action="/api/uploadFile" :on-success="onLogoSuccess" :show-file-list="false" style="display: inline-flex">
            <i class="el-icon-plus"></i>
            <img :src="formData.logoUrl" alt="">
          </el-upload>
          <span> 128*128  png </span>
        </el-form-item>
        <el-form-item label="" prop="bannerUrl">
          <el-upload class="upload-box banner" name="picture" action="/api/uploadFile" :on-success="onBannerSuccess" :show-file-list="false" style="display: inline-flex">
            <i class="el-icon-plus"></i>
            <img :src="formData.bannerUrl" alt="">
          </el-upload>
          <span> 600 </span>
        </el-form-item>
        <el-form-item label="" prop="shortDesc">
          <el-input v-model="formData.shortDesc" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="tag-item" label="" prop="tagList">
          <el-tag class="tag-text" :key="index" v-for="(tag, index) in formData.tagList" closable :disable-transitions="false" @close="removeTag(index)"> {{tag}} </el-tag>
          <el-input v-if="tagInputVisible" class="tag-input" ref="tagInput" v-model="inputTag" size="small" @keyup.enter.native="addTag" @blur="addTag" >
          </el-input>
            <el-button v-else class="tag-btn" size="small" @click="showTagInput">+ </el-button>
        </el-form-item>
        <el-form-item label="" prop="foundDate" required>
          <el-date-picker v-model="formData.foundDate" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="abstract">
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 8}" v-model="formData.abstract" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="whitePaperUrl" required>
          <el-upload class="upload-box" name="whitePaper" action="/api/uploadFile" :on-success="onWhitePaperSuccess" :show-file-list="false" style="display: inline-flex" accept="*.pdf">
            <i class="el-icon-plus"></i>
            <img :src="formData.whitePaperUrl ? '/storage/static/pdf.jpg' : ''" alt="">
          </el-upload>
          <a :href="formData.whitePaperUrl" target="_blank" style="color: red;"></a>
          <span> pdf </span>
        </el-form-item>
        <el-form-item :label="select.label" v-for="(select, field) in selectList" :key="field" required>
          <el-select v-model="formData[field]">
            <el-option v-for="option in select.optionList" :key="option.value" :label="option.label" :value="option.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="fundStartTime">
          <el-date-picker v-model="formData.fundStartTime" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="fundEndTime">
          <el-date-picker v-model="formData.fundEndTime" type="date" placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="fundStage">
          <el-select v-model="formData.fundStage">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <!--<el-form-item label="" prop="tokenName">-->
          <!--<el-input v-model="formData.tokenName" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="" prop="tokenSymbol">-->
          <!--<el-input v-model="formData.tokenSymbol" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="">-->
          <!--<el-input v-model="formData.tokenPrice" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="">
          <el-input v-model="formData.contractAddr" placeholder="ERC20"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.companyEmail" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.companyAddr" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="footer-btn-area">
      <el-button type="primary" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      inputTag: '',
      tagList: [],
      tagInputVisible: false,
      selectList: {},
      formData: {
        projId: '',
        nameCn: '',
        nameEn: '',
        shortDesc: '',
        abstract: '',
        homeUrl: '',
        logoUrl: '',
        bannerUrl: '',
        tagList: '',
        whitePaperUrl: '',
        region: '',
        buzType: '',
        stage: '',
        foundDate: '',
        fundStartTime: '',
        fundEndTime: '',
        fundStage: '',
        companyEmail: '',
        companyAddr: '',
        tokenName: '',
        tokenSymbol: '',
        tokenPrice: '',
        contractAddr: ''
      },
      ruleList: {
        nameCn: [
          { required: true, message: '', triger: 'blur' }
        ],
        nameEn: [
          { required: true, message: '', triger: 'blur' }
        ],
        homeUrl: [
          { required: true, message: '', triger: 'blur' }
        ],
        shortDesc: [
          { required: true, message: '', triger: 'blur' }
        ],
        abstract: [
          { required: true, message: '', triger: 'blur' }
        ],
        region: [
          { required: true, message: '', triger: 'blur' }
        ],
        buzType: [
          { required: true, message: '', triger: 'blur' }
        ],
        stage: [
          { required: true, message: '', triger: 'blur' }
        ],
        tokenName: [
          { required: true, message: '', triger: 'blur' }
        ],
        tokenSymbol: [
          { required: true, message: '', triger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    this.formData.projId = this.$route.params.id
    this.$http.get('/api/getProjTagList').then((res) => {
      if (res.data.errcode === 0) {
        this.selectList = res.data.data
      }
    })
    this.updateData()
  },
  methods: {
    updateData: function () {
      this.$http.post('/api/getProjBasicInfo', {
        projId: this.formData.projId
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.formData = res.data.data
          this.formData.projId = res.data.data.id
        }
      })
    },
    onLogoSuccess (res) {
      if (res.errcode === 0) {
        this.formData.logoUrl = res.data.url
      }
    },
    onBannerSuccess (res) {
      if (res.errcode === 0) {
        this.formData.bannerUrl = res.data.url
      }
    },
    onWhitePaperSuccess (res) {
      if (res.errcode === 0) {
        this.formData.whitePaperUrl = res.data.url
      }
    },
    showTagInput () {
      this.tagInputVisible = true
      this.$nextTick(_ => {
        this.$refs.tagInput.$refs.input.focus()
      })
    },
    addTag () {
      if (this.inputTag) {
        this.formData.tagList.push(this.inputTag)
      }
      this.tagInputVisible = false
      this.inputTag = ''
    },
    removeTag (index) {
      this.tagList.splice(index, 1)
    },
    submit () {
      this.$refs['formData'].validate((valid) => {
        if (!valid) {
          this.$message({
            message: '',
            type: 'warning'
          })
        } else {
          this.$http.post('/api/updProjBasicInfo', this.formData).then((res) => {
            if (res.data.errcode === 0) {
              this.$message({ type: 'success', message: '!' })
              this.updateData()
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.form-container {
  padding: 0 20px;
  .tag-item {
    .tag-text {
      margin-right: 10px;
    }
    .tag-input {
      width: 90px;
    }
  }
  .upload-box.banner {
    width: 250px;
  }
}
</style>



